input[type=text], select, textarea {
    position: relative;
    font-family: 'Consolas';
    background: transparent;
    display: block;
    width: 100%;
    border: 2px solid white;
    border-radius: 4px;
    resize: vertical;
    color: white;
    position:relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    padding: 5px;
}
  
#btn-submit {
  position: relative;
  font-family: 'Consolas';
  background: transparent;
  display: block;
  border: 2px solid white;
  border-radius: 4px;
  cursor: pointer;
  background-color: white;
  color: #252526;
  top: 50%;
  left: 50%;
  padding: 5px;
  margin: 5px;
  transform: translate(-50%);
}

#btn-submit:hover {
  border: 2px solid #32bcb0;
  background-color: #32bcb0;
  color: #252526;
}