.nav-buttons h1
{
    color: #32bcb0;
    font-family: 'Consolas';
    font-size: 14px;
    display: inline 
}
.nav-buttons a 
{
    color: #ce8349;
    font-family: 'Consolas';
    text-decoration: none;
}
.nav-buttons a:hover
{
    color: #ce8349;
    text-decoration: underline;
}
.nav-buttons
{
    position: absolute;
    left:50%;
    transform: translate(-50%);
    padding: 10px;
    font-family: 'Consolas';
    color: white;
    background-color: #333333;
    font-size: 14px;
    border-radius: 4px;
}
.nav-bar
{
    position: fixed;
    z-index: 1000;
    background-color: #333333;
    width: 100vw;
}


