/* CONTENT */
@media only screen and (min-width: 900px){
    .social-bar{
        position: fixed;
        bottom: 0;
    }
    .social-buttons{
        transform: translate(0%, -100%);
    }

    .social-buttons a{
        display:block;
    }
}
  @media only screen and (max-width: 899px){
    .social-bar{
        position: relative;
    }
    .social-buttons{
        margin: 10px;
        width: 100vw;
        text-align: center;
    }
    .social-buttons a{
        display:inline;
    }
  }

.social-bar{
    z-index: 1000;
}
.social-buttons{
    position: absolute;
}
.social-buttons a{
    text-align: center;
    background-color: transparent;
    border: none;
    color: white;
    padding: 12px 16px;
    font-size: 20px;
    cursor: pointer;
  }
  
  .social-buttons a:hover {
      color: #32bcb0;
  }