/* MAIN */

html {
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background-color: #1e1e1e;
  margin: 0;
}

#page{
  position: relative;
}

/* SECTIONS */

#home
{
  position:relative;
  background-color: #1e1e1e;
  width: 100vw;
  height: 100vh;
}
#about
{
  position:relative;
  background-color: #252526;
  width: 100vw;
  height: 100vh;
}
#portfolio
{
  position:relative;
  background-color: #1e1e1e;
  width: 100vw;
  height: 100vh;
}
#contact
{
  position:relative;
  background-color: #252526;
  width: 100vw;
  height: 100vh;
}

.particle{
  z-index: 1;
  position: absolute;
  height: 100vh;
  width: 100vw;
}

.content{
  z-index: 3;
  position: relative;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  }

/* CONTENT */
@media only screen and (min-width: 900px){
  .content{
    width: 60vw;
  }
}
@media only screen and (max-width: 899px){
  .content{
    width: 90vw;
  }
}

.content h1{
  color: #fff; 
  font-family: 'Consolas';
  font-size: 40px;
  font-weight: normal;
  display: block;
}

.content span
{
  color: #ce8349; 
  font-family: 'Consolas';
  font-size: 40px;
  display: block;
}

.content h2{
  margin: 0;
  padding: 0;
  color: #fff; 
  font-family: 'Consolas';
  font-size: 20px;
  font-weight: normal;
  display: block;
}

.content h3{
  text-align: left;
  color: #fff; 
  font-family: 'Consolas';
  font-size: 14px;
  font-weight: normal;
  margin: 15px;
  white-space: pre-line;
  display: block;
}


/* EXPERIENCE */
.experience{
  position: relative;
  border: 2px dotted white;
  border-radius: 4px;
  text-align: left;
}

@media only screen and (min-width: 900px){
  .exp-btns{
    margin-top: 10px;
    margin-bottom: 5px;
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
    vertical-align:top;
    position: relative;
    float: left;
  }

  .exp-btns button {

    background-color: transparent;
    border: 1px transparent;
    color: white;
    cursor: pointer;
    display: block;
    font-family: 'Consolas';
    font-size: 11px;
    text-align: center;
    position: relative;
    margin: auto;
  }

  .exp-text {
    border-left: 2px dotted white;
    position: relative;
    color: white;
    font-family: 'Consolas';
    text-align: left;
    overflow: hidden;
  }
}

@media only screen and (max-width: 899px){
  .exp-btns{
    position: relative;
    display: block;
    height: 30px;
    overflow:auto;
    white-space: nowrap;
  }

  .exp-btns button {
    background-color: transparent;
    border: 1px transparent;
    color: white;
    cursor: pointer;
    font-family: 'Consolas';
    font-size: 11px;
    position: relative;
    display: inline;
  }

  .exp-text {
    position: relative;
    color: white;
    font-family: 'Consolas';
    text-align: left;
    display:inline-block;
  }
}

.exp-btns button:hover {
  font-weight:bold;
  color: white;
  border-bottom: 2px dotted #32bcb0;
}

.current{
  border-bottom: 2px dotted #32bcb0 !important;
}

.exp-field h1{
  color: #32bcb0;
  font-family: 'Consolas';
  font-size: 14px;
  display: inline 
}

.carousel{
  width: 100%;
  height: 75vh;
}

.carousel-inner{
  width: 75%;
  left: 50%;
  transform: translate(-50%);
}

.carousel-item a{
  display: block;
  font-size: 20px;
  color: white;
}

.carousel-item a:hover{
  color: #32bcb0;
}

@media only screen and (max-width: 899px)
{
  .slide-image{
    display:none !important;
  }
}

@media only screen and (min-width: 900px)
{
  .slide-link{
    display:none !important;
  }
}

img{
  opacity: 0.5;
  display: block;
}
img:hover{
  opacity:1;
}

.image-cover{
  position: relative;
  background-color:#32bcb0;
}